import { useAcademicDepartmentAllClassMaster } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import CustomSelectAcademicStandard from "../../../Ui/Select/CustomSelectAcademicStandard";
import { useTranslation } from "react-i18next";

const FeeStatisticsAcademicStandardFilter = ({
  did,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
}) => {
  const { t } = useTranslation();
  const { academicDepartmentAllClassMaster } =
    useAcademicDepartmentAllClassMaster({
      data: {
        did: did,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !did,
    });
  return (
    <CustomSelectAcademicStandard
      selectLabel={t("select_standard")}
      selectedValue={t("select_standard_placeholder")}
      options={
        academicDepartmentAllClassMaster?.list?.length > 0
          ? [...academicDepartmentAllClassMaster?.list]
          : []
      }
      onClick={onSelect}
      isFull
      errorShow={errorShow}
      defalutValue={defalutValue}
      isDenied={isDenied}
    />
  );
};

export default FeeStatisticsAcademicStandardFilter;
