import { useInstituteAllAcademicDepartment } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import CustomSelectDepartment from "../../../Ui/Select/CustomSelectDepartment";
import { useTranslation } from "react-i18next";

const FeeStatisticsAcademicDepartmentFilter = ({
  instituteId,
  onSelect,
  onlyDepartment,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();

  const { instituteAllAcademicDepartment } = useInstituteAllAcademicDepartment({
    data: {
      id: instituteId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !instituteId,
  });
  return (
    <>
      {onlyDepartment ? (
        <CustomSelectDepartment
          selectLabel={t("select_department")}
          selectedValue={t("select_department_placeholder")}
          options={
            instituteAllAcademicDepartment?.list?.length > 0
              ? instituteAllAcademicDepartment?.list
              : []
          }
          onClick={onSelect}
          isFull
          errorShow={errorShow}
          defalutValue={defalutValue}
          viewAs="NORMAL_FILTER"
          isDenied={isDenied}
          customStyleContainer={customStyleContainer}
        />
      ) : (
        <CustomSelectDepartment
          selectLabel={t("select_department")}
          selectedValue={t("select_department_placeholder")}
          options={
            instituteAllAcademicDepartment?.list?.length > 0
              ? [
                  "ALL",
                  "By Bank",
                  "Particular Student",
                  ...instituteAllAcademicDepartment?.list,
                ]
              : ["ALL", "By Bank", "Particular Student"]
          }
          onClick={onSelect}
          isFull
          errorShow={errorShow}
          customStyleContainer={customStyleContainer}
        />
      )}
    </>
  );
};

export default FeeStatisticsAcademicDepartmentFilter;
