import { useAcademicDepartmentAllBatch } from "../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { useTranslation } from "react-i18next";
import CustomSelectUniversalBatch from "../../../Ui/Select/CustomSelectUniversalBatch";

const FeeStatisticsUniversalBatchFilter = ({
  did,
  onSelect,
  onlyBatch,
  errorShow,
  defalutValue,
  isDenied,
}) => {
  const { t } = useTranslation();

  const { academicDepartmentAllBatch } = useAcademicDepartmentAllBatch({
    data: {
      did: did,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !did,
  });
  return (
    <>
      {onlyBatch ? (
        <CustomSelectUniversalBatch
          selectLabel={t("select_batch")}
          selectedValue={t("select_batch_placeholder")}
          options={
            academicDepartmentAllBatch?.list?.length > 0
              ? academicDepartmentAllBatch?.list
              : []
          }
          onClick={onSelect}
          isFull
          errorShow={errorShow}
          defalutValue={defalutValue}
          isDenied={isDenied}
        />
      ) : (
        <CustomSelectUniversalBatch
          selectLabel={t("select_batch")}
          selectedValue={t("select_batch_placeholder")}
          options={
            academicDepartmentAllBatch?.list?.length > 0
              ? ["ALL", ...academicDepartmentAllBatch?.list]
              : ["ALL"]
          }
          onClick={onSelect}
          isFull
          errorShow={errorShow}
        />
      )}
    </>
  );
};

export default FeeStatisticsUniversalBatchFilter;
