import { useTranslation } from "react-i18next";
import style from "../../../FinanceManager/Income/Income.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import FeeStatisticsDepartmentFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsDepartmentFilter";
import FeeStatisticsBatchFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsBatchFilter";
import FeeStatisticsStandardFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsStandardFilter";
import {
  batchLabel,
  departmentLabel,
  departmentTypeLabel,
} from "../../../../Constant/Statistics/statistics_constant";
import { useCallback } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import CustomSelect from "../../../../Ui/Select/CustomSelect";
import { casteCategory } from "../../../../../constant/constant_value";
import FeeStatisticsAcademicDepartmentFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsAcademicDepartmentFilter";
import FeeStatisticsUniversalBatchFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsUniversalBatchFilter";
import FeeStatisticsAcademicStandardFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsAcademicStandardFilter";
const PendingFeesSendReminderFilter = ({
  applyFilter,
  setApplyFilter,
  onClose,
  fid,
  onActionEventFire,
  instituteId,
}) => {
  const { t } = useTranslation();

  const onDepartmentTile = (val) => {
    setApplyFilter((prev) => ({
      ...prev,
      all_depart: val,
      depart: "",
      batch_status: val,
      batch: "",
      master: "",
    }));
  };
  const onDepartmentTypeTile = (val) => {
    setApplyFilter((prev) => ({
      ...prev,
      departmentType: val,
      all_depart: "",
      depart: "",
      batch_status: "",
      batch: "",
      master: "",
    }));
  };
  const onBatchTile = (val) => {
    setApplyFilter((prev) => ({ ...prev, batch_status: val, batch: "" }));
  };
  const onCatseSelect = (val) => {
    setApplyFilter((prev) => ({ ...prev, category: val }));
  };
  const onSelectBatch = useCallback((value) => {
    setApplyFilter((prev) => ({
      ...prev,
      batch_status: "PARTICULAR_BATCH",
      batch: value,
    }));
  }, []);
  const onSelectStandard = useCallback((value) => {
    setApplyFilter((prev) => ({ ...prev, master: value }));
  }, []);

  const onSelectDepartment = useCallback((value) => {
    setApplyFilter((prev) => ({
      ...prev,
      all_depart: "PARTICULAR",
      depart: value,
    }));
  }, []);

  return (
    <Modal>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("apply_filter")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <TileWrapper label="department">
            {departmentTypeLabel?.map((tile) => (
              <TileCard
                key={tile?.key}
                tile={tile}
                onTile={onDepartmentTypeTile}
                activeTile={applyFilter?.departmentType}
              />
            ))}
          </TileWrapper>
          {applyFilter?.departmentType === "Normal" ? (
            <>
              <TileWrapper label="department_type">
                {departmentLabel?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onDepartmentTile}
                    activeTile={applyFilter?.all_depart}
                  />
                ))}
              </TileWrapper>
              {applyFilter?.all_depart === "PARTICULAR" ? (
                <>
                  <FeeStatisticsDepartmentFilter
                    fid={fid}
                    onSelect={onSelectDepartment}
                    onlyDepartment
                    defalutValue={applyFilter?.depart?.dName}
                  />
                  <TileWrapper label="batch_type">
                    {batchLabel?.map((tile) => (
                      <TileCard
                        key={tile?.key}
                        tile={tile}
                        onTile={onBatchTile}
                        activeTile={applyFilter?.batch_status}
                      />
                    ))}
                  </TileWrapper>

                  {applyFilter?.batch_status === "PARTICULAR_BATCH" ? (
                    <FeeStatisticsBatchFilter
                      did={applyFilter?.depart?._id}
                      onSelect={onSelectBatch}
                      onlyBatch
                      defalutValue={applyFilter?.batch?.batchName}
                    />
                  ) : null}
                  <FeeStatisticsStandardFilter
                    did={applyFilter?.depart?._id}
                    onSelect={onSelectStandard}
                    instituteId={instituteId}
                    defalutValue={applyFilter?.master?.className}
                  />
                </>
              ) : null}
              <CustomSelect
                selectLabel={t("select_caste_catgory")}
                selectedValue={t("select_caste_catgory_placeholder")}
                options={casteCategory}
                onClick={onCatseSelect}
                defalutValue={applyFilter.category}
              />
              <CreateButton
                label="next"
                onAction={() => onActionEventFire("Normal")}
              />
            </>
          ) : applyFilter?.departmentType === "Academic" ? (
            <>
              <FeeStatisticsAcademicDepartmentFilter
                instituteId={instituteId}
                onSelect={onSelectDepartment}
                onlyDepartment
                defalutValue={applyFilter?.depart?.dName}
              />
              <FeeStatisticsUniversalBatchFilter
                did={applyFilter?.depart?._id}
                onSelect={onSelectBatch}
                onlyBatch
                defalutValue={applyFilter?.batch?.batchName}
              />
              <FeeStatisticsAcademicStandardFilter
                did={applyFilter?.depart?._id}
                onSelect={onSelectStandard}
                defalutValue={
                  applyFilter?.master?.className
                    ? `${applyFilter?.master?.className} (${
                        applyFilter?.master?.department?.dName ?? ""
                      })`
                    : ""
                }
              />
              <CreateButton
                label="next"
                onAction={() => onActionEventFire("Academic")}
              />
            </>
          ) : null}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default PendingFeesSendReminderFilter;
